.calc {
    &-item {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        padding: 10px 0;
        flex-flow: row nowrap;
        border-bottom: 1px solid rgba($color: $colorText, $alpha: 0.2);
        &:nth-child(even) {
            background-color: rgba(#000, 0.1);
        }
        &__wrapper {
            flex-direction: column;
            align-items: center;
            justify-content: center;
            margin-top: 30px;
        }
        .input-wrapper {
            width: 150px;
            display: flex;
            align-items: center;
            justify-content: center;
            border-right: 1px solid rgba($color: $colorText, $alpha: 0.2);
            font-family: $fontPrimary;
            font-weight: 500;
            font-size: 16px;
            input {
                text-align: center;
                font-size: 14px;
                font-weight: 700;
                padding: 0 5px;
                padding-bottom: 2px;
                background-color: transparent;
                border-bottom: 1px solid rgba($color: $colorText, $alpha: 0.2);
                width: 50px;
                transition: 0.5s all ease-in;
                &:focus {
                    border-bottom-color: $colorText;
                }
            }
        }
        &_title {
            background-color: $colorPrimary;
            color: white;
            border-bottom: 1px solid white;
            .calc {
                &-title,
                &-price,
                &-measure {
                    color: #fff;
                    border-color: #fff;
                }
            }
            .input-wrapper {
                border-color: #fff;
            }
        }
    }
    &-title,
    &-price,
    &-measure {
        font-family: $fontPrimary;
        font-size: 16px;
        color: $colorText;
        font-weight: 500;
        border-right: 1px solid rgba($color: $colorText, $alpha: 0.2);
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100px;
    }
    &-title {
        border-left: 1px solid rgba($color: $colorText, $alpha: 0.2);
        width: 500px;
        justify-content: flex-start;
        padding-left: 15px;
    }
}

.calculator {
    position: relative;
    .sum {
        text-align: center;
        position: absolute;
        background-color: $colorLight;
        opacity: 0;
        z-index: -1;
        top: -5px;
        border: 2px solid rgba(0, 0, 0, .2);
        right: 0;
        padding: 15px;
        transition: 0.5s all linear;
        &.active {
            opacity: 1;
            transition: .3s all linear;
            z-index: 5;
        }
    }
    .total-price {
        text-align: center;
        color: $colorText;
        font-family: $fontPrimary;
        font-weight: 500;
        font-size: 16px;
        span {
            display: block;
            font-size: 18px;
            font-weight: 600;
            margin-top: 10px;
        }
    }
    .btn {
        &_primary {
            font-size: 14px;
            width: 200px;
            height: 40px;
            margin-top: 15px;
        }
    }
}