.form {
    &-calc {
        display: flex;
        align-items: center;
        flex-direction: column;
        justify-content: center;
        width: 100%;
        &__input-block {
            width: 100%;
            display: flex;
            flex-wrap: wrap;
        }
        &__input-wrapper {
            flex-basis: 50%;
            display: flex;
            padding: 0 30px;
            &:nth-child(odd) {
                justify-content: flex-end;
            }
        }
        &__input {
            padding: 5px 10px;
            margin-top: 15px;
            font-weight: 500;
            font-size: 20px;
            font-family: $fontPrimary;
            background-color: transparent;
            width: 250px;
            border-bottom: 2px solid $colorText;
        }
    }
    &-contact {
        padding: 30px;
        background-color: rgba(0, 0, 0, 0.02);
        border-radius: 5px;
        &__title {
            text-align: center;
            margin-bottom: 30px;
            padding-bottom: 10px;
            font-size: 28px;
            position: relative;
            &::before {
                content: '';
                position: absolute;
                height: 2px;
                width: 250px;
                left: 50%;
                margin-left: -125px;
                bottom: 0;
                background-color: $colorPrimary;
            }
        }
        &__input {
            width: 450px;
            height: 40px;
            font-family: $fontPrimary;
            padding: 0 10px;
            font-weight: 500;
            font-size: 18px;
            &-wrapper {
                margin-top: 30px;
                &:first-child {
                    margin-top: 0;
                }
            }
        }
        &__textarea {
            height: 100px;
            padding: 10px;
            resize: none;
        }
    }
    &-select {
        border: none;
        width: 250px;
        outline: none;
        border: 2px solid $colorPrimary;
        padding: 5px 10px;
        font-family: $fontPrimary;
        option {
            width: 250px;
            border: none;
        }
    }
    &-total {
        margin-top: 30px;
        .section__text {
            text-align: center;
        }
    }
}