@media screen and(max-width:1660px) {
    .slider {
        &-work {
            .slider__item {
                &-next {
                    margin-left: -300px;
                }
                &-prev {
                    left: -300px;
                }
            }
        }
    }
}

@media screen and(max-width:1200px) {
    .slider {
        &-work {
            .slider__item {
                &-next {
                    margin-left: -300px;
                }
                &-prev {
                    left: -300px;
                }
                &-active {
                    z-index: 10;
                }
            }
        }
    }
    .container {
        padding: 0 15px;
    }
    .section {
        &-number {
            .tile {
                &-block {
                    justify-content: space-around;
                }
            }
        }
        &-about {
            .row {
                align-items: center;
                justify-content: center;
                &_two_col {
                    .column {
                        flex-basis: 80%;
                        &:nth-child(2) {
                            margin-top: 30px;
                            align-items: center;
                            justify-content: center;
                        }
                    }
                }
            }
            .section__img {
                width: 100%;
                img {
                    width: 100%;
                }
            }
            .btn {
                &_primary {
                    margin: 0 auto;
                    margin-top: 30px;
                }
            }
        }
        &-services {
            .tile-block {
                justify-content: space-around;
            }
        }
        &-contact {
            .row {
                align-items: center;
                justify-content: center;
                .column {
                    &:nth-child(2) {
                        margin-top: 30px;
                    }
                }
            }
        }
    }
}

@media screen and(max-width:768px) {
    body {
        &.overflow {
            overflow: hidden;
        }
    }
    .section {
        &-about {
            .section__title {
                align-items: center;
                justify-content: center;
            }
        }
        &__title {
            text-align: center;
        }
        &-services {
            .tile {
                &-block {
                    &__item {
                        margin-bottom: 0;
                    }
                }
            }
        }
    }
    .slider {
        &-work {
            height: 500px;
            .slider__item {
                &-active {
                    left: 0;
                    margin-left: 0;
                    z-index: 5;
                    opacity: 1;
                    transform: translateX(0);
                }
                &-next {
                    transform: translateX(100%);
                    opacity: 0;
                }
                &-prev {
                    transform: translateX(-100%);
                    opacity: 0;
                }
                .slider__img {
                    img {
                        width: 100%;
                        height: 500px;
                    }
                }
            }
        }
        &-services {
            .slick-slide {
                margin: 0 10px;
            }
            .tile {
                &__img {
                    img {
                        width: 100%;
                        height: 100%;
                        object-fit: cover;
                    }
                }
            }
        }
        .next,
        .prev {
            opacity: 1;
            height: 50px;
            background-color: $colorPrimary;
            border-radius: 50%;
            top: 50%;
            margin-top: -25px;
        }
        .next::before,
        .prev::before {
            font-size: 20px;
            top: 1px;
        }
        .next::before {
            left: 3px;
        }
        .prev::before {
            right: 2px;
        }
    }
    .main-nav {
        &_fixed {
            height: 60px;
        }
        &_mobile {
            .main-nav__list {
                .custom-list__item {
                    display: block;
                }
            }
            &.active {
                opacity: 1;
                z-index: 9999;
                border-bottom-color: transparent;
            }
        }
        &__list {
            .custom-list {
                &__item {
                    display: none;
                }
                &__logo {
                    display: block;
                }
            }
            .main-nav__burger {
                display: block;
            }
        }
        .custom-list_horizontal {
            justify-content: space-around;
        }
    }
    .tabs-control {
        justify-content: flex-start;
    }
    .calculator {
        .sum {
            top: 0px;
            padding: 10px 15px;
        }
    }
    .calc-title {
        width: 300px;
    }
}

@media screen and(max-width:650px) {
    .tabs-content__item {
        overflow-y: scroll;
        align-items: flex-start;
    }
    .calculator {
        .sum {
            display: none;
            width: 80%;
            margin: 0 auto;
            margin-top: 30px;
            transform: translateY(-50%);
            &.active {
                position: relative;
                transform: translateY(0);
                display: block;
            }
        }
    }
    .calc-title {
        width: 300px;
    }
    .tabs-control {
        justify-content: center;
    }
}

@media screen and(max-width:500px) {
    .section {
        &-welcome {
            h1 {
                font-size: 40px;
            }
        }
    }
    .form-calc {
        &__input {
            &-wrapper {
                flex-basis: 100%;
                justify-content: center;
                &:nth-child(odd) {
                    justify-content: center;
                }
            }
        }
    }
    .form-contact {
        width: 100%;
        &__input {
            width: 100%;
        }
    }
    .row_two_col {
        .column {
            flex-basis: 100%;
        }
    }
    .section {
        &-number {
            .tile {
                &-block {
                    flex-direction: column;
                }
            }
            .tile+.tile {
                margin-top: 30px;
            }
        }
    }
    .main-nav {
        &_mobile {
            .contact-list {
                flex-direction: column;
                flex-wrap: unset;
                &__item {
                    flex-basis: 100%;
                }
            }
        }
    }
    .slider {
        &-work {
            height: 300px;
            .slider__item {
                .slider__img {
                    img {
                        height: 300px;
                    }
                }
            }
        }
    }
}

@media screen and (max-width:320px) {
    .form {
        &-contact {
            padding: 30px 0;
        }
        &-calc {
            &__input {
                &-wrapper {
                    padding: 0 15px;
                }
            }
        }
    }
    .section {
        &-about {
            .column:nth-child(2) {
                display: none;
            }
        }
    }
}