body {
    color: $colorText;
    font-family: $fontAccent;
    background-color: $colorLight;
    overflow-x: hidden;
}

.container {
    max-width: 1200px;
    width: 100%;
    margin: 0 auto;
}

.welcome-img {
    position: relative;
    img {
        width: 100%;
        height: 100vh;
        min-height: 600px;
        object-fit: cover;
    }
    &::before {
        content: '';
        position: absolute;
        width: 100%;
        height: 100%;
        background: linear-gradient(180deg, rgba(34, 34, 34, 0.14) 1.13%, rgba(34, 34, 34, 0.469136) 38.31%, rgba(34, 34, 34, 0.702716) 64.71%, #222222 98.3%);
    }
}

.section {
    &:nth-child(even) {
        background-color: rgba($colorText, 0.05);
    }
    &-welcome {
        height: 100vh;
        background: url(../img/welcome.png) center center;
        background-size: cover;
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
        h1 {
            font-family: $fontPrimary;
            font-size: 72px;
            color: #fff;
            text-align: center;
        }
        .section__content {
            position: absolute;
            height: 100%;
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            top: 0;
            left: 0;
            padding: 0;
            background: linear-gradient(180deg, rgba(34, 34, 34, 0.14) 1.13%, rgba(34, 34, 34, 0.469136) 38.31%, rgba(34, 34, 34, 0.702716) 64.71%, #222222 98.3%);
        }
    }
    &-about {
        .section__title {
            align-items: flex-start;
            justify-content: flex-start;
        }
        .row {
            &_two_col {
                .column {
                    justify-content: space-between;
                }
            }
        }
    }
    &-number {
        .section__text {
            text-align: center;
        }
        .tile-block {
            margin-top: 0;
        }
    }
    &-services {
        .tile {
            &-block__item {
                margin-bottom: 30px;
            }
        }
    }
    &__content {
        padding: 60px 0;
    }
    &__title {
        font-family: $fontPrimary;
        font-size: 40px;
        text-transform: uppercase;
        padding: 25px 0;
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        font-weight: 500;
        &::before {
            top: 0;
            position: absolute;
            content: '';
            width: 200px;
            height: 2px;
            background-color: $colorPrimary;
        }
    }
    &__text {
        font-size: 18px;
        text-align: justify;
        &_uppercase {
            text-transform: uppercase;
        }
        &_primary {
            font-family: $fontPrimary;
        }
        &_medium {
            font-weight: 500;
        }
        &_bold {
            font-weight: bold;
        }
        &_size_m {
            font-size: 24px;
        }
        &_margin_m {
            margin-top: 15px;
        }
        &.price {
            font-size: 36px;
            font-weight: bold;
        }
    }
}

.row {
    display: flex;
    flex-wrap: wrap;
    &_margin_middle {
        margin-top: 30px;
    }
    &_two_col {
        .column {
            display: flex;
            flex-direction: column;
            flex-basis: 50%;
            &:nth-child(2) {
                align-items: flex-end;
            }
        }
    }
}

.d-flex {
    &_align_center {
        align-items: center;
    }
    &_justify_center {
        justify-content: center;
    }
}

.btn {
    width: 300px;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    &_center {
        margin: 0 auto;
    }
    &_primary {
        background-color: $colorPrimary;
        text-transform: uppercase;
        font-family: $fontPrimary;
        font-weight: 500;
        font-size: 24px;
        color: #fff;
        margin-top: 30px;
    }
}

.tile {
    &-block {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        margin-top: 30px;
    }
    &__info {
        text-align: center;
        padding: 30px 0;
        background-color: rgba(#fff, 0.4);
        border: 2px solid #bdc3c7;
        border-top: 0;
    }
    &__title {
        font-family: $fontAccent;
        font-size: 20px;
        text-transform: uppercase;
    }
    &__price {
        font-family: $fontPrimary;
        font-weight: 700;
        font-size: 20px;
        margin-top: 15px;
    }
}

.slider {
    position: relative;
    &-work {
        display: flex;
        justify-content: space-between;
        height: 400px;
        margin-top: 30px;
        overflow: hidden;
    }
    .slider__item {
        position: absolute;
        opacity: 0;
        transition: 1s all linear;
        z-index: -1;
        transform: translateX(100%);
        margin-left: 0;
        &::before {
            content: '';
            position: absolute;
            width: 100%;
            opacity: 1;
            height: 100%;
            left: 0;
            top: 0;
            background-color: transparent;
            transition: 1s all linear;
        }
        &-prev,
        &-active,
        &-next {
            z-index: 5;
            transition: 1s all linear;
            opacity: 1;
            transform: translateX(0);
        }
        &-prev::before,
        &-next::before {
            background-color: rgba(0, 0, 0, 0.5);
        }
        &-prev {
            left: -100px;
        }
        &-active {
            left: 50%;
            margin-left: -300px;
        }
        &-next {
            left: 100%;
            margin-left: -500px;
        }
    }
    .slider__img {
        overflow: hidden;
        img {
            width: 600px;
            height: 400px;
            object-fit: cover;
        }
    }
    .prev,
    .next {
        height: 100%;
        width: 50px;
        background-color: rgba(0, 0, 0, 0.6);
        color: #222;
        z-index: 999;
        top: 0;
        opacity: 0;
        cursor: pointer;
        position: absolute;
        transition: 0.5s all linear;
    }
    .next {
        right: 0;
    }
    .prev {
        left: 0;
    }
    .prev:hover,
    .next:hover {
        background-color: rgba(0, 0, 0, 0.8);
    }
    .next::before,
    .prev::before {
        position: absolute;
        height: 100%;
        width: 100%;
        font-size: 30px;
        display: flex;
        align-items: center;
        justify-content: center;
        font-family: FontAwesome;
        color: #fff;
    }
    .prev::before {
        content: '\f053 ';
    }
    .next::before {
        content: '\f054 ';
    }
    &:hover {
        .prev,
        .next {
            opacity: 1;
        }
    }
}

.contact-list {
    &__item+&__item {
        margin-top: 20px;
    }
    &__item {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: flex-start;
        &::before {
            font-family: FontAwesome;
            border-radius: 50%;
            margin-right: 10px;
            font-size: 30px;
            width: 30px;
            height: 30px;
            display: block;
        }
        &-phone::before {
            content: '\f098 ';
        }
        &-email::before {
            content: '\f0e0  ';
        }
        &-whatsApp::before {
            content: '\f232';
        }
    }
    &__link {
        font-size: 18px;
        font-family: $fontPrimary;
        font-weight: 500;
        border-bottom: 1px solid $colorPrimary;
        color: $colorText;
    }
}