.main-nav {
    width: 100%;
    z-index: 999;
    background-color: transparent;
    border-bottom: 2px solid;
    border-bottom-color: transparent;
    transition: 0.5s all ease-in-out;
    top: 30px;
    &_fixed {
        position: fixed;
        display: flex;
        height: 50px;
        align-items: center;
        justify-content: center;
    }
    &_mobile {
        position: fixed;
        top: 0;
        opacity: 0;
        z-index: -2;
        height: 100%;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        background-color: rgba(#000, 0.99);
        &.active {
            background-color: rgba(#000, 0.99);
        }
        .custom-list {
            margin-top: auto;
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            &__item:nth-child(2) {
                margin-left: 0;
            }
            &__item {
                margin-top: 20px;
            }
            &__link {
                font-size: 18px;
            }
        }
        .contact-list {
            margin-top: auto;
            margin-bottom: 30px;
            display: flex;
            flex-wrap: wrap;
            align-items: center;
            justify-content: center;
            text-align: center;
            &__item {
                flex-basis: 50%;
                margin-top: 0;
                display: flex;
                justify-content: center;
                align-items: center;
                margin-top: 30px;
            }
            &__item::before {
                color: #fff;
            }
            &__link {
                padding-bottom: 5px;
                color: #fff;
                border-bottom: 2px solid #Fff;
                transition: 0.3s all ease-in;
            }
        }
    }
    &.active {
        background-color: rgba($colorText, 0.8);
        border-bottom-color: #bdc3c7;
        top: 0;
        .custom-list {
            &__link {
                font-size: 18px;
            }
        }
    }
    &__burger {
        display: none;
        a {
            color: #fff;
            font-size: 40px;
        }
    }
}

.nav-close {
    position: absolute;
    right: 30px;
    top: 15px;
    &__link {
        color: #fff;
        font-size: 30px;
    }
}

.custom-list {
    &_horizontal {
        display: flex;
        align-items: center;
        justify-content: center;
    }
    &__item {
        padding: 5px 10px;
        &:last-child {
            padding-right: 0;
        }
        &:nth-child(2) {
            margin-left: auto;
        }
    }
    &__logo {
        padding: 0;
    }
    &__link {
        color: #fff;
        font-family: $fontPrimary;
        font-size: 18px;
        text-transform: uppercase;
        font-weight: 600;
    }
}