.tabs {
    width: 100%;
}

.tabs-control {
    display: flex;
    padding: 10px 0;
    justify-content: center;
    &__item+&__item {
        margin-left: 15px;
    }
    &__item {
        cursor: pointer;
        font-size: 20px;
        font-family: $fontPrimary;
        padding: 5px 15px;
        display: flex;
        justify-content: center;
        align-items: center;
        border-bottom: 2px solid $colorPrimary;
        &.active {
            background-color: $colorPrimary;
            color: #fff;
        }
    }
}

.tabs-content {
    &__item {
        display: none;
        &.active {
            display: flex;
        }
    }
}